import React from "react";

const CryptoWork = () => {
    return (
        <div className="content-wrapper">
            <div className="crypto-work-wrapper">
                <div className="content">
                    <h2 className="content-text-heading">Currency-Panel.</h2>
                    <h3 className="content-text-subheading">
                        A dashboard for monitoring and viewing statistical data on crypto currency traded to customers.
                    </h3>
                </div>
                <div className="poster">
                    <img src="static/img/stack/php.png" className="php-icon" alt="" />
                    <img src="static/img/stack/stylus.png" className="stylus-icon" alt="" />
                    <div className="card">
                        <img className="card-img" src="static/img/works/currency.png" alt="" />
                        <div className="card-description">
                            <a href="http://cryptobroker.gherciu.gq" target="_blank" className="card-btn">
                                <img src="static/img/icons/see.svg" />
                                View demo
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <style jsx>{`
                .php-icon {
                    max-width: 500px;
                    height: auto;
                    position: absolute;
                    bottom: -120px;
                    right: 20px;
                    transform: rotate(20deg);
                }
                .stylus-icon {
                    max-width: 200px;
                    height: auto;
                    position: absolute;
                    top: 20px;
                    right: -100px;
                    transform: rotate(90deg);
                }

                .crypto-work-wrapper {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 0px;
                    align-items: center;
                }
                h3 {
                    margin-top: 10px;
                }
                .content {
                    max-width: 390px;
                }
                .poster {
                    max-width: 560px;
                    width: 100%;
                    padding: 20px 0px;
                    display: flex;
                    justify-content: center;
                    position: relative;
                }
                .card {
                    max-width: 480px;
                    z-index: 2;
                }
                @media (max-width: 860px) {
                    .crypto-work-wrapper {
                        flex-direction: column;
                    }
                    .content-text-heading {
                        text-align: center;
                    }
                    .content-text-subheading {
                        text-align: center;
                    }
                    .stylus-icon {
                        display: none;
                    }
                    .php-icon {
                        display: none;
                    }
                }
            `}</style>
        </div>
    );
};

export default CryptoWork;
